import { useState } from "react";
import { Link } from "react-router-dom";
import "./Lookup_My_Certificate2.scss"
import Certificate from "./Certificate";
import axios from "axios";


export default function LookupCertificate() {
  const [Search_id, setSerach_id] = useState("");
  const [Nextpage, setNextpage] = useState(false);
  const [Backpage, setBackpage] = useState(false);

  const submit = async () => {
    await axios.post("/web/license", {//axios  spring post
      name: Search_id
    }).then(function (response) {
          window.alert(" ", {
        position: "top-center",
        autoClose: 2000
      });
      setTimeout(() => {
        setNextpage(true);
      }, 0);
    }
    )
      //  catch 
      .catch(function (error) {
        window.alert("   .", {
          position: "top-center",
        });
      });

  };

  function Goto_back() {//
    setBackpage(true);
  }

  var date = new Date();
  var year = date.getFullYear();
  var month = date.getMonth() + 1;
  var day = date.getDate();

  //     0  format
  var monthformat = ("00" + month.toString()).slice(-2);
  var dayformat = ("00" + day.toString()).slice(-2);

  return (
    <div>{
      Backpage === false ?
        (Nextpage === false ? (
          <div className="Lookup_Container">
            <div className="Lookup_content_container2">
              <div className="Lookup_top_text">  </div>
              <div id="lookup_id_name">   </div>
              <input
                type="text"
                id="lookup_input_id"
                onChange={(e) => setSerach_id(e.target.value)}
                required
                placeholder="   "
              ></input>
              <div className="Lookup_button_line">
                <button id="search_btn" onClick={submit}></button>
                <button id="cancle_btn" onClick={Goto_back} ></button>
              </div>
            </div>
          </div>
        ) : (
          <div className="Lookup_Container">
            <div className="Lookup_content_container2">
              <div className="Lookup_top_text">  </div>
              <img
                id="user_profile_img"
                src="./Main_cocktail/Lookup_Certificate/user_profile_img.png"
                alt="user_profile_img"
              />
              <div id="lookup_mid_text">VWS <a>  </a> </div>
              <div id="lookup_mid_text"> .</div>
              <div id="lookup_Date">{year}.{monthformat}.{dayformat}</div>
              <button id="lookup_close_btn" onClick={Goto_back}></button>
            </div>
          </div>
        ))
        : <Certificate />}
    </div>
  );
}
